import bus from '../../assets/js/bus'
import store from "../index";

const app = {
    state: {
        // 国际化语言
        lang:[],
        //当前用户语言环境信息
        langType:{},
        //标题
        headerTitle:'',
        // 侧边栏开关
        slider: {
            opened: true
        },
        //选择页面
        pageNum:0,
        //用户等级，游客0或者管理员1
        userClass:0,
        //是否登录
        isLogin:true,
        //用户id
        userId:'',
        //已经发送了
        hasSend:false,
        //搜索框缓存
        globalSearchSave:'',relationSearchSave:'',
        //知识图谱缓存
        globalMapSave:{},relationMapSave:{},
        //知识图谱缓存旧
        globalOldMapSave:{},relationOldMapSave:{},
        //选择框缓存
        globalSelectList:[], relationSelectList:[],
        //已选择缓存
        globalSelect:[],relationSelect:[],

        //保存的图谱数据
        saveMapList:[],relationSaveMapList:[],
        //保存的图谱数据位置
        saveMapListPos:0,relationSaveMapListPos:0,
        //保存选择项目列表
        saveSelectList:[],relationSaveSelectList:[],
        //保存选择项目位置
        saveSelectListChose:[],relationSaveSelectListChose:[],

        //保存自动标注界面数据
        saveRelationAutoData:{},
        //保存人工标注界面数据
        saveRelationManualData:{},
    },
    mutations: {
        //保存自动标注界面数据
        updateSaveRelationAutoData(state,saveRelationAutoData) {
            state.saveRelationAutoData = saveRelationAutoData;
        },
        //保存人工标注界面数据
        updateSaveRelationManualData(state,saveRelationManualData) {
            state.saveRelationManualData = saveRelationManualData;
        },
        //更新图谱数据
        updateSaveMapList(state,saveMapList) {
            state.saveMapList = saveMapList;
        },
        //更新图谱数据
        updateRelationSaveMapList(state,relationSaveMapList) {
            state.relationSaveMapList = relationSaveMapList;
        },
        //更新图谱数据的位置
        updateSaveMapListPos(state,saveMapListPos) {
            state.saveMapListPos = saveMapListPos;
            // console.log(saveMapListPos);
        },
        updateRelationSaveMapListPos(state,relationSaveMapListPos) {
            state.relationSaveMapListPos = relationSaveMapListPos;
            // console.log(saveMapListPos);
        },
        //更新选择列表
        updateSaveSelectList(state,saveSelectList) {
            state.saveSelectList = saveSelectList;
            // console.log(saveMapList);
        },
        updateRelationSaveSelectList(state,relationSaveSelectList) {
            state.relationSaveSelectList = relationSaveSelectList;
            // console.log(saveMapList);
        },
        //更新选择列表
        updateSaveSelectListChose(state,saveSelectListChose) {
            state.saveSelectListChose = saveSelectListChose;
            // console.log(saveMapListPos);
        },
        updateRelationSaveSelectListChose(state,relationSaveSelectListChose) {
            state.relationSaveSelectListChose = relationSaveSelectListChose;
            // console.log(saveMapListPos);
        },
        //更新当前用户语言环境信息
        updateLangType(state,obj) {
            state.langType = obj;
        },
        //已经发送
        updateHasSend(state,hasSend) {
            state.hasSend = hasSend;
        },
        //更新用户等级
        updateUserClass(state,userClass) {
            state.userClass = userClass;
        },
        //是否登录
        updateLoginState(state,isLogin) {
            state.isLogin = isLogin;
        },
        //更新用户id
        updateUserId(state,userId) {
            state.userId = userId;
        },
        //  更新国际化变更后的信息
        updateLang(state,lang){
            state.lang = lang;
        },
        //更新页面序号
        updatePageNum(state,pageNum){
            state.pageNum = pageNum;
        },
        //更新标题
        updateTitle(state,title){
            state.headerTitle = title;
        },
        //更新搜索缓存
        updateSearchSave(state,globalSearchSave){
            state.globalSearchSave = globalSearchSave;
        },
        updateRelationSearchSave(state,relationSearchSave){
            state.relationSearchSave = relationSearchSave;
        },
        //更新知识图谱
        updateMapSave(state,globalMapSave){
            state.globalMapSave = globalMapSave;
        },
        updateRelationMapSave(state,relationMapSave){
            state.relationMapSave = relationMapSave;
        },
        //更新知识图谱
        updateOldMapSave(state,globalOldMapSave){
            state.globalOldMapSave = globalOldMapSave;
        },
        updateRelationOldMapSave(state,relationOldMapSave){
            state.relationOldMapSave = relationOldMapSave;
        },
        //更新选择框
        updateSelectList(state,globalSelectList){
            state.globalSelectList = globalSelectList;
        },
        updateRelationSelectList(state,relationSelectList){
            state.relationSelectList = relationSelectList;
        },
        //更新选择框
        updateSelect(state,globalSelect){
            state.globalSelect = globalSelect;
        },
        updateRelationSelect(state,relationSelect){
            state.relationSelect = relationSelect;
        },
        //  修改侧边栏展开收起
        TOGGLE_SIDEBAR: state => {
            state.slider.opened = !state.slider.opened;
        },
    }
};

export default app;
